.taskForm {
  display: flex;
  flex-direction: column;
  max-width: 200px;
  margin: auto; }

.taskName, .dueDate {
  display: flex;
  flex-direction: row; }

.name {
  width: 55%; }

.date {
  display: inline-block;
  width: 75px; }

.description {
  height: 100px; }

.button {
  color: white;
  padding: 6px;
  min-width: 90px;
  border: 1px solid black;
  background-color: #2B78E4;
  border-style: none;
  border-radius: 5px;
  margin: auto; }

.filter {
  color: white;
  padding: 6px;
  min-width: 90px;
  border: 1px solid black;
  background-color: #2B78E4;
  border-style: none;
  border-radius: 5px; }

.activeFilter {
  color: white;
  padding: 6px;
  min-width: 90px;
  border: 1px solid black;
  background-color: #2B78E4;
  border-style: none;
  border-radius: 5px; }



