/*I am not applying the hashes to these for testing purposes. 
I couldn't figure out how to get jest to work with css modules, 
and I didn't want to spend too much time trying to figure it out.
*/
:global(.task) {
  position: relative;
  max-width: 300px;
  margin: auto;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  background-color: white; }

:global(.pastDue) {
  position: relative;
  max-width: 300px;
  margin: auto;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  background-color: red; }

:global(.dueToday) {
  position: relative;
  max-width: 300px;
  margin: auto;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  background-color: yellow; }

:global(.dueLater) {
  position: relative;
  max-width: 300px;
  margin: auto;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  background-color: green; }

.checkbox {
  display: inline-block;
  position: absolute;
  left: 0; }

.clickable {
  display: inline-block;
  width: 80%; }
  .clickable:hover {
    cursor: pointer; }

.name:hover {
  cursor: pointer; }

.due:hover {
  cursor: pointer; }

.remove {
  display: inline-block;
  position: absolute;
  right: 0; }



